<template>
  <div class="import-trades">
    <Form ref="importForm" :model="importModel" :label-width="160">
      <FormItem label="" prop="jsonFile">
        <Upload :before-upload="processJSONUploadFile" action="#">
          <Button type="ghost" icon="ios-cloud-upload-outline" :disabled="!succeed">JSON</Button>
        </Upload>
      </FormItem>
      <FormItem label="JSON">
        <Input v-model="importModel.jsonContent" type="textarea" :autosize="{minRows: 8,maxRows: 30}" placeholder="JSON" :disabled="!succeed"></Input>
        <Button type="text" @click="importModel.jsonContent=''"></Button>
      </FormItem>
      <FormItem>
        <Button type="primary" :disabled="!succeed" @click="submitContent"></Button>
      </FormItem>
    </Form>
  </div>
</template>

<script>
export default {
  name: 'import-trades',
  data () {
    return {
      importModel: {
        jsonFile: '',
        jsonContent: ''
      },
      succeed: true
    }
  },
  watch: {
  },
  mounted () {
  },
  methods: {
    processJSONUploadFile (file) {
      this.readFile(file)
      return false
    },
    readFile (file) {
      let reader = new FileReader()
      reader.readAsText(file)
      reader.onload = () => {
        let content = reader.result
        this.importModel.jsonContent = content
      }
    },
    submitContent () {
      this.succeed = false
      if (!this.importModel.jsonContent) {
        this.$Modal.error({
          title: '!',
          content: 'JSON'
        })
        this.succeed = true
        return false
      } else {
        let modelConv = null
        try {
          modelConv = JSON.parse(this.importModel.jsonContent)
          console.log(modelConv)
        } catch (err) {
          this.$Modal.error({
            title: '!',
            content: 'JSON'
          })
          this.succeed = true
          return false
        }
        // if (!(modelConv instanceof Array) || !modelConv.length) {
        //   this.$Modal.error({
        //     title: '!',
        //     content: 'JSON[trade_fullinfo_get_response:{trade: {...}}]'
        //   })
        //   this.succeed = true
        //   return false
        // } else {
        try {
          this.apiItem = {
            apiHost: '',
            apiService: 'trades',
            apiAction: 'push', // push: tao11.la | update: 
            apiQuery: {}
          }
          this.apiData = JSON.parse(this.importModel.jsonContent)
          this.$store.dispatch('setAPIStore', this.apiItem)
          var apiUrl = this.$store.getters.apiUrl
          this.$http.post(apiUrl, this.apiData).then(res => {
            if (res.data.status === 'ok') {
              this.$Modal.success({
                title: '!',
                content: res.data.message
              })
              this.succeed = true
            } else {
              this.$Modal.error({
                title: '!',
                content: res.data.message
              })
              this.succeed = true
            }
          }).catch(err => {
            console.log(err)
            this.$Modal.error({
              title: '!',
              content: err.message
            })
            this.succeed = true
          })
        } catch (err) {
          console.log(err)
          this.$Modal.error({
            title: '!',
            content: err.message
          })
          this.succeed = true
        }
        // }
      }
    }
  }
}
</script>

<style lang="less" scoped>

</style>



