<template>
  <div class="buyer-settings">
    <Form ref="buyerSettingForm" :model="settings" :label-width="160">
      <!-- <FormItem label="" prop="isEnable">
        <i-switch v-model="settings.isEnable"></i-switch>
      </FormItem> -->
      <FormItem label="">
        <i-switch v-model="autoTracerSwitch"></i-switch>
      </FormItem>
      <FormItem label="(min)">
        <Input v-model="autoTracerInterval" style="width: 80px"></Input>
      </FormItem>
      <FormItem label="" prop="defaultBuyerMessage">
        <Input type="textarea" :rows="3" placeholder="" v-model="settings.defaultBuyerMessage"></Input>
        <p class="font-grey"></p>
        <Button type="primary" @click="setDefaultBuyerMessage"></Button>
      </FormItem>
      <FormItem label="" prop="defaultMemoFlag">
        <RadioGroup v-model="settings.defaultMemoFlag">
          <Radio :label="0"><span :style="{color:getMemoColor(0)}"></span></Radio>
          <Radio :label="1"><span :style="{color:getMemoColor(1)}"></span></Radio>
          <Radio :label="2"><span :style="{color:getMemoColor(2)}"></span></Radio>
          <Radio :label="3"><span :style="{color:getMemoColor(3)}"></span></Radio>
          <Radio :label="4"><span :style="{color:getMemoColor(4)}"></span></Radio>
          <Radio :label="5"><span :style="{color:getMemoColor(5)}"></span></Radio>
        </RadioGroup>
        <p class="font-grey"></p>
        <Button type="primary" @click="setDefaultMemoFlag"></Button>
      </FormItem>
    </Form>
  </div>
</template>

<script>
export default {
  name: 'buyer-settings',
  data () {
    return {
      settings: {
        // 
        isEnable: true,
        defaultBuyerMessage: '',
        defaultMemoFlag: 3 // 0-5,
      },
      succeed: false,
      autoTracerInterval: 10, // 
      autoTracerSwitch: this.$store.getters.user.tracelogisticsEnable
    }
  },
  watch: {
    'settings.isEnable': function (newVal) {
      if (this.succeed) {
        this.setEnable(newVal)
      }
    },
    'autoTracerInterval': function (newVal) {
      this.$emit('on-autotracerinterval', newVal)
    },
    'autoTracerSwitch': function (newVal) {
      this.$emit('on-autotracerswitch', newVal)
    }
  },
  mounted () {
    this.getBuyer()
    this.settings.defaultBuyerMessage = this.$store.getters.defaultBuyerMessage || ''
    this.settings.defaultMemoFlag = this.$store.getters.defaultMemoFlag || 0
  },
  methods: {
    async getBuyer () {
      this.apiItem = {
        apiHost: '',
        apiService: 'buyers',
        apiAction: 'getbyuserid',
        apiQuery: {}
      }
      this.apiData = {
        userid: this.$store.getters.user.userid
      }
      this.$store.dispatch('setAPIStore', this.apiItem)
      var apiUrl = this.$store.getters.apiUrl
      this.succeed = false
      await this.$http.post(apiUrl, this.apiData).then(res => {
        if (res.data.status === 'ok') {
          let buyer = res.data.data[0]
          this.settings.isEnable = buyer.is_enable
        } else {
          this.$Message.error('(' + res.data.message + ')')
        }
      }).catch(err => {
        console.log(err)
        this.$Message.error(',')
      })
      this.succeed = true
    },
    setEnable (val) {
      this.apiItem = {
        apiHost: '',
        apiService: 'buyers',
        apiAction: 'enable',
        apiQuery: {}
      }
      this.apiData = {
        userid: this.$store.getters.user.userid,
        is_enable: val
      }
      this.succeed = true
      this.$store.dispatch('setAPIStore', this.apiItem)
      var apiUrl = this.$store.getters.apiUrl
      this.$http.post(apiUrl, this.apiData).then(res => {
        if (res.data.status === 'ok') {
          this.$Modal.success({
            title: '!',
            content: null
          })
          this.succeed = true
        } else {
          this.$Modal.error({
            title: '!',
            content: res.data.message
          })
          this.succeed = false
          this.settings.isEnable = !val
        }
      }).catch(err => {
        console.log(err)
        this.$Modal.error({
          title: '!',
          content: err.message
        })
        this.succeed = false
        this.settings.isEnable = !val
      })
    },
    setDefaultBuyerMessage () {
      this.apiItem = {
        apiHost: '',
        apiService: 'users',
        apiAction: 'setdefaultbuyermessage',
        apiQuery: {}
      }
      this.apiData = {
        userid: this.$store.getters.user.userid,
        defaultbuyermessage: this.settings.defaultBuyerMessage
      }
      this.succeed = true
      this.$store.dispatch('setAPIStore', this.apiItem)
      var apiUrl = this.$store.getters.apiUrl
      this.$http.post(apiUrl, this.apiData).then(res => {
        if (res.data.status === 'ok') {
          this.$Modal.success({
            title: '!',
            content: null
          })
          this.succeed = true
          this.$store.dispatch('setDefaultBuyerMessage', this.settings.defaultBuyerMessage)
        } else {
          this.$Modal.error({
            title: '!',
            content: res.data.message
          })
          this.succeed = false
        }
      }).catch(err => {
        console.log(err)
        this.$Modal.error({
          title: '!',
          content: err.message
        })
        this.succeed = false
      })
    },
    getMemoColor (flag) {
      // 0-5 
      switch (flag) {
        case 0:
          return '#666'
        case 1:
          return 'red'
        case 2:
          return 'orange'
        case 3:
          return 'forestgreen'
        case 4:
          return 'blue'
        case 5:
          return 'deeppink'
        default:
          return '#666'
      }
    },
    setDefaultMemoFlag () {
      this.apiItem = {
        apiHost: '',
        apiService: 'users',
        apiAction: 'setdefaultmemoflag',
        apiQuery: {}
      }
      this.apiData = {
        userid: this.$store.getters.user.userid,
        defaultmemoflag: this.settings.defaultMemoFlag
      }
      this.succeed = true
      this.$store.dispatch('setAPIStore', this.apiItem)
      var apiUrl = this.$store.getters.apiUrl
      this.$http.post(apiUrl, this.apiData).then(res => {
        if (res.data.status === 'ok') {
          this.$Modal.success({
            title: '!',
            content: null
          })
          this.succeed = true
          this.$store.dispatch('setDefaultMemoFlag', this.settings.defaultMemoFlag)
        } else {
          this.$Modal.error({
            title: '!',
            content: res.data.message
          })
          this.succeed = false
        }
      }).catch(err => {
        console.log(err)
        this.$Modal.error({
          title: '!',
          content: err.message
        })
        this.succeed = false
      })
    }
  }
}
</script>

<style lang="less" scoped>

</style>



